import jsPDF from "jspdf";
(function (jsPDFAPI) {
var font = "";
var callAddFont = function () {
this.addFileToVFS("NotoSansEthiopic-Regular-normal.ttf", font);
this.addFont("NotoSansEthiopic-Regular-normal.ttf", "NotoSansEthiopic-Regular", "normal");
};
jsPDFAPI.events.push(['addFonts', callAddFont])
 })(jsPDF.API);